import React from "react";
import {connect} from "react-redux";
import _ from "lodash";
import ProductListSingleProductComponent from "eshop/modules/simfree/components/list/ProductListSingleProductComponent";
import ProductListHeaderFilterTagComponent
    from "eshop/modules/simfree/components/list/ProductListHeaderFilterTagComponent";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {changeCategory, getProductList} from "eshop/modules/simfree/actions/app";
import ProductFilterView from "eshop/modules/simfree/components/ProductFilterView";
import ProductListOneTimeFilterMobileComponent
    from "eshop/modules/simfree/components/filter/ProductListOneTimeFilterMobileComponent";
import {
    etProductListData,
    getActualFilters,
    getAllVisibility,
    getCurrentPageData,
    getFilterConfiguration,
    getInitiallyFiltered,
    getIsSalesOfGoodsPage,
    getMatchToDataList,
    getOpenFilterModal,
    getProductListData,
    getProductListWrapper,
    getProductsTrees,
    getSelectedCategory,
    getSelectedFilter,
    getSelectedMaxMonthlyPrice,
    getSelectedMaxMonthlyPriceRange,
    getSelectedNumberFilter,
    getSelectedOneTimePrices,
    getSelectedPrice,
    getSelectedProducer,
    getSelectedSort,
    getSelectedStickerFilter,
    getSortDefinition,
    getTieredMaxMonthlyPriceForCurrentOfferType,
    getTieredOneTimePriceForCurrentOfferType
} from "../../selectors";
import {
    changeCurrentPageProps,
    changeFilterAttribute,
    changeFilterNumberAttribute,
    changePriceFilter,
    changeStickerFilter,
    changeStickerFilterAttribute,
    clearAttrsFilters,
    clearMonthlyPrices,
    clearOneTimePrices,
    fetchFilterConfiguration,
    getFilteredProductList,
    getFilteredProductTree,
    getFilters,
    getMatchToData,
    getProductsTree,
    reloadMatchToFilter,
    selectMaxMonthlyPrice,
    selectOneTimePrice,
    setAllVisibilityProduct,
    setOpenFilterModal,
    setSelectedModel,
    setSelectedProducer,
    setSelectedRecomendedProducer,
    setSelectedSort
} from "eshop/modules/simfree/actions/filter";
import ProducerFilterView from "eshop/modules/simfree/components/filter/ProducerFilterView";
import StickerFilterView from "eshop/modules/simfree/components/filter/StickerFilterView";
import ProductCategoryTreeView from "eshop/modules/simfree/components/ProductCategoryTreeView";
import {pickPropsBy} from "eshop/modules/core/utils/ui";
import DiscountInfoSection from "eshop/modules/simfree/components/list/DiscountInfoSection";
import ProductListMaxMonthlyMobileFilterComponent
    from "eshop/modules/simfree/components/filter/ProductListMaxMonthlyMobileFilterComponent"
import {getAddTerminalToOfferBundleNo} from "eshop/modules/cart/selectors";
import {filterLabelsHardcodedAsGross} from "eshop/modules/simfree/selectors";

class ProductListView extends React.Component {
    constructor(props) {
        super(props);
    }

    _buildFilterStateQueryParam(){
        let result='selectedCategory=' + this.props.selectedCategory;
        if(this.props.selectedProducer){
            result += '&producer=' + this.props.selectedProducer;
        }
        if(this.props.selectedPrice.from){
            result += '&priceFrom=' + this.props.selectedPrice.from;
        }
        if(this.props.selectedPrice.to){
            result += '&priceTo=' + this.props.selectedPrice.to;
        }
        if(this.props.selectedNumberFilters.Displaysize_from){
            result += '&Displaysize_from=' + this.props.selectedNumberFilters.Displaysize_from;
        }
        if(this.props.selectedNumberFilters.Displaysize_to){
            result += '&Displaysize_to=' + this.props.selectedNumberFilters.Displaysize_to;
        }
        if(this.props.selectedFilters.operatingsystem){
            result += '&operatingsystem=' + this.props.selectedFilters.operatingsystem;
        }
        if(this.props.selectedFilters.processorCores){
            result += '&processorCores=' + this.props.selectedFilters.processorCores;
        }
        if(this.props.selectedSort){
            result += '&sortMode=' + this.props.selectedSort;
        }
        if(this.props.selectedMaxMonthlyPriceRange && this.props.selectedMaxMonthlyPriceRange.to){
            result += '&maxMonthlyCost=' + this.props.selectedMaxMonthlyPriceRange.to;
        }
        if(this.props.selectedFilters.waterproofclalss){
            result += '&waterproofclalss=' + this.props.selectedFilters.waterproofclalss;
        }
        if(this.props.selectedFilters.dustprotection){
            result += '&dustprotection=' + this.props.selectedFilters.dustprotection;
        }
        if(this.props.selectedFilters.DualSim){
            result += '&DualSim=' + this.props.selectedFilters.DualSim;
        }
        return encodeURIComponent(result);
    }

    componentDidMount() {
        if (this.props.products.data) {
            this._loadModules();
        }
        this.props.changeCategory({code : this.props.queryParams.selectedCategory});
    }

    componentDidUpdate(prevProps, prevState){
        if (!_.isEqual(this.props.products,prevProps.products) && this.props.products.data) {
            if(prevProps.currentPage!=this.props.currentPage) {
                this._loadModules();
            }
            this._loadFixerModule();
        }

        if(!this.props.openFilterModal && prevProps.openFilterModal){
            this._loadModules();
        }
        if(this.props.openFilterModal && !prevProps.openFilterModal){
            this._loadModalModules();
        }
    }

    checkSendDataLayer(currentProps, prevProps){
        return currentProps.selectedSort !== prevProps.selectedSort && currentProps.isAllVisible !== prevProps.isAllVisible &&
               currentProps.selectedCategory !== prevProps.selectedCategory && currentProps.selectedProducer !== prevProps.selectedProducer &&
               currentProps.currentPage !== prevProps.currentPage && currentProps.selectedPrice !== prevProps.selectedPrice;
    }

    handleInputBlur(input){
        if(input > this.props.products.pagesCount){
            input = this.props.products.pagesCount;
        } else if(input < 1){
            input = 1;
        }
        this.props.getProductList(input);
    }

    handleInputChange(input){
        if(!isNaN(input)){
            this.props.changeCurrentPage(input);
        }
    }

    handleShowAll(){
        if(!this.props.isAllVisible){
            this.props.setAllVisibility(true);
            this.props.getProductList(1);
        }
    }

    handleShowStandardGrid(){
        if(this.props.isAllVisible){
            this.props.setAllVisibility(false);
            this.props.getProductList(1);
        }

    }




    handlePreviousPage(page){
        if(this.props.products.currentPage > 1){
            this.props.getProductList(page);
        }
    }

    handleNextPage(page){
        if(this.props.products.currentPage != this.props.products.pagesCount && this.props.products.pagesCount > 1){
            this.props.getProductList(page);
        }
    }

    handleGivenPage(page){
        this.props.getProductList(page);
    }

    handleSortChooseAction(selectedSort){
        this.props.setSelectedSort(selectedSort);
        this.props.getProductList(1);
    }

    openModal() {
        this.props.setOpenFilterModal(true);
        if(document.getElementById("mobile-expander-module-one-time")){
            OPL.UI.stopModules(document.getElementById("mobile-expander-module-one-time"));
            OPL.UI.initModules(document.getElementById("mobile-expander-module-one-time"));
        }
        if(document.getElementById("mobile-expander-module-monthly")){
            OPL.UI.stopModules(document.getElementById("mobile-expander-module-monthly"));
            OPL.UI.initModules(document.getElementById("mobile-expander-module-monthly"));
        }
    }

    closeModal() {
        this.props.setOpenFilterModal(false);

    }

    checkSeparatorVisibility(){
        return (this.props.products.pagesCount > 6) && (this.props.products.currentPage > 4);
    }

    calculatePages(){
        var currentPage = this.props.products.currentPage;
        var pagesCount = this.props.products.pagesCount;
        var pages;
        if (pagesCount < 6){
            pages = _.range(2,pagesCount+1);
        } else {
            if(currentPage < 4){
                pages = _.range(2,6);
            } else if(currentPage > 3 && currentPage < pagesCount-1) {
                pages = _.range(currentPage-2,currentPage+3);
            } else {
                pages = _.range(pagesCount-4, pagesCount+1);
            }
        }
        return pages;
    }

    clearFilters(){
        this.props.clearMonthlyPrices();
        this.props.clearOneTimePrices();
        this.props.setSelectedProducer("");
        this.props.clearAttrFilters();
        this.props.changePriceFilter("from", "");
        this.props.changePriceFilter("to", "");
        this.props.reloadTree();
    }

    render() {
        let discountInfo = pickPropsBy(this.props.descriptions,(name) => name.indexOf("discountInfo_") === 0);
        if (this.props.products.data) {
            return (
                <div className="u-margin-top-l-xl">
                    {this._renderHeader()}
                    <div id="product-list-wrapper" className="l-row opl-product-box--bordered-small__wrapper opl-product-box--bordered-medium__wrapper u-no-margin">
                        <div id={'product-list-opl-layout-fixer'} data-js-module="core/modules/layout-fixer" data-js-options={JSON.stringify({
                                        conditions: "element:{seen}",
                                        selectors: [
                                        ".js-layout-fixer-group-1",
                                        ".js-layout-fixer-group-2",
                                        ".js-layout-fixer-group-3",
                                        ".js-layout-fixer-group-4",
                                        ".js-layout-fixer-group-5",
                                        ".js-layout-fixer-group-6",
                                        ".js-layout-fixer-group-7",
                                        ".js-layout-fixer-group-8",
                                        ".js-layout-fixer-group-9",
                                        ".js-layout-fixer-group-10"
                                    ]})}>
                            {this._renderProductTable()}
                        </div>
                    </div>

                    {this._renderFooter()}
                    <DiscountInfoSection content={discountInfo}/>
                </div>
            )
        } else
            return null;
    }

    _renderHeader(){
        return (
            <div className="l-row u-margin">
                <div className="l-col l-col-12  u-small-hide u-medium-hide">
                    <ProductListHeaderFilterTagComponent />
                </div>
                <div className="l-col l-col-12 u-spacing u-small-hide u-medium-hide">
                    <div className="o-separator u-no-spacing u-no-padding"></div>
                </div>
                {this._renderFilterModal()}
                <div className="l-col l-col-3  u-spacing-top-s u-small-hide u-medium-hide">
                    <p className="g-gray5-c"></p>
                </div>
                <div className="l-col l-col-6  u-text-center u-small-hide u-medium-hide">
                    {this._renderPagination()}
                </div>
                <div className="l-col l-col-small-8 l-col-medium-8 l-col-3 ">
                    <div className="o-select opl-select-light u-right u-small-right">
                        <select className="u-padding-s u-padding-top-s"
                                value={this.props.selectedSort}
                                onChange={event => {this.handleSortChooseAction(event.target.value)}}>
                            {this.props.options && this.props.options.map(function (option) {
                                return <option key={option.value} value={option.value}>{option.description}</option>
                            })}
                        </select>
                        <span className="o-select__arrow"></span>
                    </div>
                </div>
                <div className="l-col l-col-12  u-spacing-top">
                    <div className="o-separator u-no-spacing u-no-padding"></div>
                </div>
            </div>)
    }

    _renderPagination() {
        return (
            <div className="opl-pagination opl-pagination__with-input opl-pagination__radius u-inline-block">
                <p className="opl-pagination__prev">
                    <a href="#!" title={'previous page'}
                       onClick={this.handlePreviousPage.bind(this,this.props.products.currentPage-1)}
                       className={this.props.products.currentPage == 1 ? "opl-pagination__disabled": ""}
                       rel={"prev"}>
                        <span className="g-icon g-icon--only g-icon--xs g-icon--arrow-left"></span>
                    </a></p>
                <label className="u-no-padding">
                    <input type="text"
                           value={this.props.currentPage}
                           placeholder={this.props.products.currentPage}
                           className="opl-input--size-s u-inline-block"
                           onBlur={event => {this.handleInputBlur(event.target.value)}}
                           onKeyPress={event => {if (event.key === 'Enter') {this.handleInputBlur(event.target.value)}}}
                           onChange={event => {this.handleInputChange(event.target.value)}}/>
                </label>
                <p className="u-padding-left">
                    <span className="u-spacing-right-s">z</span>
                    <a href="#!"
                       className="u-inline"
                       onClick={this.handleGivenPage.bind(this,this.props.products.pagesCount)}>
                        {(this.props.products.pagesCount == 0) ? this.props.products.pagesCount+1 : this.props.products.pagesCount}
                    </a>
                </p>
                <p className="opl-pagination__next">
                    <a href="#!" title={'next page'}
                       onClick={this.handleNextPage.bind(this,this.props.products.currentPage+1)}
                       className={(this.props.products.currentPage == this.props.products.pagesCount ||
                           this.props.products.pagesCount == 0) ? "opl-pagination__disabled" : ""}
                       rel={"next"}>
                        <span className="g-icon g-icon--only g-icon--xs g-icon--arrow-right"></span>
                    </a>
                </p>
            </div>
        )
    }

    _renderFooter(){
        return (
            <div className="l-row u-medium-spacing-l u-small-spacing-l">
                <div className="l-col l-col-12  u-spacing-l u-medium-spacing-l u-small-spacing-l">
                    <div className="o-separator u-no-spacing u-no-padding u-small-hide u-medium-hide"></div>
                </div>
                {!(this.props.isAllVisible) ?
                    (
                        [
                        <div key="pagging" className="l-col l-col-10 l-col-small-12 u-text-center u-padding-l">
                            {this._renderPagination()}
                        </div>,
                         <div key="showAll" className="l-col l-col-2 l-col-small-12 u-text-right u-padding-l u-small-padding-right-l">
                             <a href="#!"
                                className="u-inline-block u-right u-small-right u-spacing-top-s"
                                onClick={this.handleShowAll.bind(this)}>Pokaż wszystkie
                             </a>
                         </div>
                        ]
                    )
                    :
                    (
                        <div className="l-col l-col-small-12 u-text-center u-padding-l u-small-padding-right-l">
                            {this._renderPagination()}
                            <a href="#!"
                               className="u-inline-block u-right u-small-right u-spacing-top-s"
                               onClick={this.handleShowStandardGrid.bind(this)}>Pokaż mniej
                            </a>
                        </div>
                    )
                }
                    <div className="l-col l-col-12  u-no-spacing-top u-spacing u-medium-spacing-top-l u-small-spacing-top u-small-hide u-medium-hide">
                    <div className="o-separator u-no-spacing u-no-padding"></div>
                </div>
            </div>
        )
    }

    _renderPaginationSeparator(){
        return (
            <li>...</li>
        )
    }

    _renderPaginationNumbers(pages){
        return pages.map((page) => {
            return (
                <li><a href="#"
                       className={this.props.products.currentPage === page ? "active": ""}
                       onClick={this.handleGivenPage.bind(this,page)}
                >{page}</a></li>
            )
        }, this);
    }

    _renderProductTable() {
        var instalmentTooltipDescription = this.props.descriptions && this.props.descriptions["instalmentTooltipDescription"]
                                                                               ?this.props.descriptions["instalmentTooltipDescription"]
                                                                               :"";
        return this.props.products.data.map((product, index) => {
            return (
                <ProductListSingleProductComponent key={product.productId} product={product}
                instalmentTooltipDescription={instalmentTooltipDescription} position={index}
                filterState={this._buildFilterStateQueryParam()} salesChannel={this.props.channels && this.props.channels.sales}
                products={this.props.products.data}
                cartIsFix={this.props.cartIsFix}
                queryParams={this.props.queryParams}/>
            )
        }, this);
    }

    _renderFilterModal() {
        return (
            <div className="l-col l-col-small-4 l-col-medium-4  u-large-hide">
                <div className="o-icon-list">
                	<a href="#!"
                	   className="o-icon-list__item u-font-normal u-text-decoration-none"
                	   onClick={this.openModal.bind(this, true)}>
                        <div className="o-icon-list__icon u-vertical-middle u-padding-right-s">
                            <div aria-hidden="true" className="g-icon g-icon--before g-icon--xs-s g-icon--filter"></div>
                        </div>
                        <div className="o-icon-list__text u-vertical-middle">
                            <span>Filtruj</span>
                        </div>
                    </a>
                </div>
                {this._renderModal()}
            </div>
        );
    }

    _renderModal() {
        return (
            <OraModal id={"ora-filters-modal"} open={this.props.openFilterModal} size="small"
                      onClose={this.closeModal.bind(this)} showClose={false}>
                <div className="o-modal-content">
                    <div id="ora-product-list-section-modal">
                        <h2 className="h2">Filtruj</h2>
                        <ProducerFilterView id={"opl-producer-filter-modal"} {...this.props} title={"Producent"}
                                            initiallyExpanded={true}/>
                        <ProductCategoryTreeView {...this.props} isModal={true} isMobileTree={true} title={"Kategoria"}
                                                 initiallyExpanded={true}/>
                        <ProductListMaxMonthlyMobileFilterComponent tieredPriceNumberExpander={5}
                                                                    tieredPrices={this.props.monthlyTieredPrices}
                                                                    selectedTieredPrices={this.props.selectedMonthlyTieredPrices}
                                                                    onClick={this.props.onClickMonthlyPrice}
                                                                    filterLabelsHardcodedAsGross={this.props.filterLabelsHardcodedAsGross}
                                                                    title={"Maksymalna opłata za ratę terminala"}
                                                                    initiallyExpanded={true}
                                                                    isSalesOfGoodsPage={this.props.isSalesOfGoodsPage}
                        />
                        <ProductListOneTimeFilterMobileComponent tieredPriceNumberExpander={5}
                                                                 tieredPrices={this.props.oneTimeTieredPrices}
                                                                 selectedTieredPrices={this.props.selectedOneTimeTieredPrices}
                                                                 onClick={this.props.onClickOneTimePrice}
                                                                 filterLabelsHardcodedAsGross={this.props.filterLabelsHardcodedAsGross}
                                                                 title={"Opłata na start"} initiallyExpanded={true}/>
                        <div id="ora-product-list-section-modal-product">
                            <ProductFilterView {...this.props} isModal={true} title={"Cena"} initiallyExpanded={true}/>
                        </div>
                        <StickerFilterView {...this.props} isModal={true} title={"Promocja"} initiallyExpanded={true}/>
                        <div className="o-separator u-spacing-l u-spacing-top-l"></div>
                        <a href="#" onClick={this.clearFilters.bind(this)} className="g-gray5-c">Wyczyść filtry</a>
                        <button onClick={this.closeModal.bind(this)}
                                className="o-btn opl-btn opl-btn--primary u-spacing-top-l u-w-100">
                        <span className="opl-ripple-box">
                            <span className="opl-ripple-circle"></span>
                        </span>Pokaż wyniki
                        </button>
                    </div>
                </div>
            </OraModal>
        )
    }

    _loadModules() {
        OPL.UI.stopModules(document.getElementById("ora-product-section-list-component"));
        OPL.UI.initModules(document.getElementById("ora-product-section-list-component"));

    }

    _loadModalModules() {
        OPL.UI.stopModules(document.getElementById("ora-product-list-section-modal"));
        OPL.UI.initModules(document.getElementById("ora-product-list-section-modal"));
    }

    _loadFixerModule() {
        OPL.UI.stopModules(document.getElementById("product-list-wrapper"));
        OPL.UI.initModules(document.getElementById("product-list-wrapper"));
    }

}

const mapStateToProps = (state, ownProps) => ({
    products: getProductListWrapper(state),
    options: getSortDefinition(state),
    selectedSort: getSelectedSort(state),
    openFilterModal: getOpenFilterModal(state),
    isAllVisible: getAllVisibility(state),
    selectedCategory: getSelectedCategory(state),
    filters: getActualFilters(state),
    selectedPrice: getSelectedPrice(state),
    selectedFilters: getSelectedFilter(state),
    filterConfiguration: getFilterConfiguration(state),
    selectedProducer: getSelectedProducer(state),
    selectedNumberFilters: getSelectedNumberFilter(state),
    selectedStickerFilters: getSelectedStickerFilter(state),
    isSalesOfGoodsPage: getIsSalesOfGoodsPage(state),
    currentPage: getCurrentPageData(state),
    categories: getProductsTrees(state),
    productList: getProductListData(state),
    matchToData: getMatchToDataList(state),
    queryParams: ownProps.queryParams,
    oneTimeTieredPrices : getTieredOneTimePriceForCurrentOfferType(state),
    selectedOneTimeTieredPrices : getSelectedOneTimePrices(state),
    monthlyTieredPrices : getTieredMaxMonthlyPriceForCurrentOfferType(state),
    selectedMonthlyTieredPrices : getSelectedMaxMonthlyPrice(state),
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    filterLabelsHardcodedAsGross: filterLabelsHardcodedAsGross(state),
    selectedMaxMonthlyPriceRange: getSelectedMaxMonthlyPriceRange(state),
    initiallyFiltered: getInitiallyFiltered(state)
});

const mapDispatchToProps = (dispatch) => ({
    getProductList: (page) => dispatch(getFilteredProductList(page)),
    setSelectedSort: (sort) => dispatch(setSelectedSort(sort)),
    setAllVisibility: (value) => dispatch(setAllVisibilityProduct(value)),
    setOpenFilterModal: (opened) => dispatch(setOpenFilterModal(opened)),
    reloadTree:(page) => dispatch(getFilteredProductTree(page)),
    getFilters: (selectedCategory) => dispatch(getFilters(selectedCategory)),
    changeFilterAttribute: (element, parent, multiValue, operation) => dispatch(changeFilterAttribute(element, parent, multiValue, operation)),
    changePriceFilter: (type, price) => dispatch(changePriceFilter(type, price)),
    changeStickerFilter: (value) => dispatch(changeStickerFilter(value)),
    changeFilterNumberAttribute: (type, value, element) => dispatch(changeFilterNumberAttribute(type, value, element)),
    getFilteredList: (page) => dispatch(getFilteredProductList(page)),
    setSelectedProducer: (selectedProducer) => dispatch(setSelectedProducer(selectedProducer)),
    changeStickerFilterAttribute: (element, parent, multiValue, operation) => dispatch(changeStickerFilterAttribute(element, parent, multiValue, operation)),
    clearAttrFilters: () => dispatch(clearAttrsFilters()),
    changeCurrentPage: (page) => dispatch(changeCurrentPageProps(page)),
    reloadMatchToFilter: () => dispatch(reloadMatchToFilter()),
    getMatchToData: () => dispatch(getMatchToData()),
    getCategories: (initCategory) => dispatch(getProductsTree(initCategory)),
    changeCategory: (category) => dispatch(changeCategory(category)),
    fetchConfiguration: (initProducer, initCategory, initFilters) => dispatch(fetchFilterConfiguration(initProducer, initCategory, initFilters)),
    onClickOneTimePrice: (id) => dispatch(selectOneTimePrice(id)),
    onClickMonthlyPrice: (id) => dispatch(selectMaxMonthlyPrice(id)),
    clearOneTimePrices: () => dispatch(clearOneTimePrices()),
    clearMonthlyPrices: () => dispatch(clearMonthlyPrices()),
    showError: (msg) => dispatch(showError(msg))
});

const ProductListSectionComponent = connect(mapStateToProps, mapDispatchToProps)(ProductListView);
export default ProductListSectionComponent;
